___CSS_LOADER_EXPORT___.push([module.id, `.content {
  width: 70%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 0;
}
.content .carousel {
  height: 80%;
  width: 100%;
}
.content .carousel #drag-container,
.content .carousel #spin-container {
  position: relative;
  display: flex;
  margin: auto;
  transform-style: preserve-3d;
  transform: rotateX(-10deg);
  height: 100%;
}
.content .carousel #drag-container img {
  transform-style: preserve-3d;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  line-height: 10rem;
  font-size: 2.5rem;
  text-align: center;
  border-radius: 0.2rem;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-box-reflect: below 0.5rem linear-gradient(transparent, transparent, rgba(0,0,0,0.33333));
}
.content .carousel #drag-container img:hover,
.content .carousel #drag-container video:hover {
  box-shadow: 0 0 0.75rem rgba(255,255,255,0.86667);
  -webkit-box-reflect: below 0.5rem linear-gradient(transparent, transparent, rgba(0,0,0,0.46667));
}
.content .carousel #drag-container p {
  font-family: Serif;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  color: #fff;
}
.content .carousel #ground {
  width: 45rem;
  height: 45rem;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  background: -webkit-radial-gradient(center center, farthest-side, rgba(153,153,153,0.2), transparent);
}
.content .carousel #music-container {
  position: absolute;
  top: 0;
  left: 0;
}
@keyframes spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
@keyframes spinRevert {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
.content .word {
  height: 20%;
  display: flex;
  align-items: center;
  padding-top: 2rem;
  font-family: ZCOOLXiaoWei;
}
.content .word > span {
  color: #ffbc6a;
  display: inline-block;
  font-weight: bold;
  font-size: 5.6rem;
  margin: 0rem 0.2rem;
}
.content .happy {
  font-family: Romance;
  color: #ffbc6a;
  font-size: 2.6rem;
  font-weight: bolder;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  letter-spacing: 0.2rem;
}
.content .summery {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
}
.content .name {
  font-size: 2.1rem;
  font-family: MaShanZhengRegular;
  position: relative;
  top: 0.3rem;
}
.content .name > span {
  font-family: Filxgirl;
}
.content .date {
  font-size: 2rem;
  letter-spacing: 0.2rem;
  font-family: Filxgirl;
  font-weight: bolder;
  position: relative;
}
