___CSS_LOADER_EXPORT___.push([module.id, `.displayer {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}
.displayer .wu {
  height: 100%;
  width: 20%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.displayer .wu img {
  position: absolute;
  width: 11.2rem;
  -o-object-fit: contain;
     object-fit: contain;
  transform: translateX(-50%);
  animation: wobble 8s ease-in-out infinite;
}
@keyframes wobble {
  0%,
  100% {
    transform: rotate3d(1, 1, 0, 14deg);
    transform: scale(0.9);
    opacity: 1;
  }
  25% {
    transform: rotate3d(-1, 1, 0, 14deg);
    transform: scale(0.95);
    opacity: 0.8;
  }
  50% {
    transform: rotate3d(-1, -1, 0, 14deg);
    transform: scale(1.1);
    opacity: 0.9;
  }
  75% {
    transform: rotate3d(1, -1, 0, 14deg);
    transform: scale(1);
    opacity: 1;
  }
}
.displayer .wu .huan {
  position: absolute;
  top: 10%;
}
.displayer .wu .xi {
  position: absolute;
  top: 68%;
}
.displayer .photowall {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 78%;
  height: 100%;
}
.displayer .photowall .summary {
  color: #fce1e0;
  height: 25%;
  width: 100%;
  border: 1px solid #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.displayer .photowall .summary .word {
  display: flex;
  align-items: center;
  justify-content: center;
}
.displayer .photowall .summary .word > span {
  color: #ffbc6a;
  display: inline-block;
  font-family: ZCOOLXiaoWei;
  font-weight: bold;
  /* font-family: MaShanZhengRegular; */
  font-size: 5rem;
  margin: 0rem 0.2rem;
  /* color: #ffbc6a; */
  /* padding: 0.1rem 0.2rem; */
  /* border-radius: 25rem; */
}
.displayer .photowall .summary .author {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
}
.displayer .photowall .summary .author .name {
  font-size: 2.2rem;
  font-family: MaShanZhengRegular;
  letter-spacing: 0.1rem;
}
.displayer .photowall .summary .author .name > span {
  font-family: Filxgirl;
}
.displayer .photowall .summary .author .date {
  font-size: 2.3rem;
  letter-spacing: 0.1rem;
  font-family: Filxgirl;
  font-weight: bolder;
  position: relative;
}
