  height: 25%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
}
.hlogo img {
  position: absolute;
  width: 10.5rem;
  -o-object-fit: contain;
     object-fit: contain;
  transform: translateX(-50%);
  animation: bounce 7s ease-in-out infinite;
}
@keyframes bounce {
  0%,
  100% {
    transform: rotate3d(1, 1, 0, 14deg);
    transform: scale(0.9);
    opacity: 1;
  }
  25% {
    transform: rotate3d(-1, 1, 0, 14deg);
    transform: scale(0.95);
    opacity: 0.8;
  }
  50% {
    transform: rotate3d(-1, -1, 0, 14deg);
    transform: scale(1.1);
    opacity: 0.9;
  }
  75% {
    transform: rotate3d(1, -1, 0, 14deg);
    transform: scale(1);
    opacity: 1;
  }
}
.hlogo .huan {
  position: absolute;
  top: 8%;
  left: 20%;
}
.hlogo .xi {
  position: absolute;
  right: 20%;
  top: 0%;
}
