  height: 100%;
  width: 20%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.vlogo img {
  position: absolute;
  width: 10.5rem;
  -o-object-fit: contain;
     object-fit: contain;
  transform: translateX(-50%);
  animation: bounce 8s ease-in-out infinite;
}
@keyframes bounce {
  0%,
  100% {
    transform: rotate3d(1, 1, 0, 14deg);
    transform: scale(0.9);
    opacity: 1;
  }
  25% {
    transform: rotate3d(-1, 1, 0, 14deg);
    transform: scale(0.95);
    opacity: 0.8;
  }
  50% {
    transform: rotate3d(-1, -1, 0, 14deg);
    transform: scale(1.1);
    opacity: 0.9;
  }
  75% {
    transform: rotate3d(1, -1, 0, 14deg);
    transform: scale(1);
    opacity: 1;
  }
}
.vlogo .huan {
  position: absolute;
  top: 10%;
}
.vlogo .xi {
  position: absolute;
  top: 68%;
}
