.flipImage .gallery .vertical {
  width: 10rem;
  width: var(--rect-short);
  height: 15rem;
  height: var(--rect-mid);
}
.flipImage .gallery .top {
  align-self: flex-end;
}
.flipImage .gallery .bottom {
  align-self: flex-start;
}
.flipImage .flip-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 1.2s;
  transition-timing-function: cubic-bezier(0.05, 1.2, 0.4, 1.35);
