___CSS_LOADER_EXPORT___.push([module.id, `.carouselContent {
  height: 100%;
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: scale(0);
  opacity: 0;
  transition: all 1.2s linear;
  position: relative;
}
.carouselContent .carousel {
  height: 78%;
  width: 100%;
}
.carouselContent .carousel #drag-container,
.carouselContent .carousel #spin-container {
  position: relative;
  display: flex;
  margin: auto;
  transform-style: preserve-3d;
  transform: rotateX(-10deg);
  height: 100%;
}
.carouselContent .carousel #drag-container img {
  transform-style: preserve-3d;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  line-height: 10rem;
  font-size: 2.5rem;
  text-align: center;
  border-radius: 0.2rem;
  /* box-shadow: 0 0 0.2rem #fff; */
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-box-reflect: below 0.5rem linear-gradient(transparent, transparent, rgba(0,0,0,0.33333));
}
.carouselContent .carousel #drag-container img:hover,
.carouselContent .carousel #drag-container video:hover {
  box-shadow: 0 0 0.75rem rgba(255,255,255,0.86667);
  -webkit-box-reflect: below 0.5rem linear-gradient(transparent, transparent, rgba(0,0,0,0.46667));
}
.carouselContent .carousel #drag-container p {
  font-family: Serif;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  color: #fff;
}
.carouselContent .carousel #ground {
  width: 45rem;
  height: 45rem;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  background: -webkit-radial-gradient(center center, farthest-side, rgba(153,153,153,0.2), transparent);
}
.carouselContent .carousel #music-container {
  position: absolute;
  top: 0;
  left: 0;
}
@keyframes spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
@keyframes spinRevert {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
.carouselContent .carousel .heartContent {
  position: relative;
  width: 80%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.carouselContent .carousel .heartContent .photo-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.carouselContent .carousel .heartContent .photo-container .photo {
  position: absolute;
  width: 4rem;
  height: 4rem;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 50%;
  animation: fly 10s linear infinite;
}
@keyframes heartbeat {
  to {
    transform: scale(1.1) rotate(-45deg);
  }
}
@keyframes fly {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
